/* breadcrumbs */
.breadcrumbs {
	color: $text-color-light;
	padding: 20px nth($text-layout-space, 2);
	border-bottom: 1px solid $border-color;

	@include media('<desk-md') {
		padding: 20px nth($text-layout-space--md, 2); }

	@include media('<tab-lg') {
		display: none;
		padding: 20px nth($text-layout-space--sm, 2); }

	@include media('<mob-lg') {
		padding: 20px nth($text-layout-space--mob, 2); }

	&:last-child {
		margin-bottom: 0; } }

.breadcrumbs__list {
