/* products preview */
.products__inner {
	position: relative;
	letter-spacing: $letter-spacing;
	z-index: 1;
	//outline: 1px solid #cc2300

	@include media('<mob-lg') {
		border: solid $border-color;
		border-width: 1px 0; } }

.products__animation-wrap {
	$prop: margin, transform;
	will-change: $prop;
	transition: {
		property: $prop;
		duration: $animation-speed; } }

.products__align {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%; }

.product__shadow {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: -1;
	box-shadow: 0 0 #{$products-shade-size} rgba(0, 0, 0, 0.2);
	pointer-events: none;
	will-change: opacity;
	opacity: 0;
	transition: {
		property: opacity;
		duration: $animation-speed; } }

.products__inset {
	display: block;
	position: relative;
	text-decoration: none;
	color: inherit;
	transition: none; }

.products__spacer {
	position: relative;
	@include ratio(nth($products-size, 1), nth($products-size, 2));

	@include media('<mob-md') {
		@include ratio(nth($products-size--sm, 1), nth($products-size--sm, 2)); }

	@include media('<mob-sm') {
		@include ratio(nth($products-size--mob, 1), nth($products-size--mob, 2)); }

	// Если блок не нужно масштабировать пропорционально
	//min-height: 389px
 }	//height: 100%

// фото продукта выносим за пределы блока,
// который увеличивается на ховер,
// потому что, если изображение находится внутри,
// то при увеличении оно дергается
.products__visual-wrap {
	position: absolute;
	left: 0;
	top: 0;
	right: 0; }

.products__visual {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	background-color: white; }

.products__figure {
