/* events-slider */
.events-slider {
	&.swiper-container {
		@include padding-vertical(nth($events-rang-size, 1)+$events-shade-size); }

	// чтобы слайды не растягивало по высоте на 100%
	.swiper-wrapper {
		align-items: flex-start; } }

.events-slider-prev, .events-slider-next, .events-slider-pagination, .events-slider__content {
	user-select: none; }

.events-slider__item {
	// чтобы не разваливалось до загрузки слайдера
	width: 320px; }

.events-slider__inner {
	display: block;
	position: relative;
	text-decoration: none;
	color: inherit;
	@include ratio(nth($events-size, 2), nth($events-size, 1));
	will-change: margin, transform;
	transition: {
		property: transform, margin;
		duration: $animation-speed; } }

.events-slider__figure {
	position: absolute;
	left: 0;
	right: 0;
	top: 0; }

.events-slider__img {
	position: relative;
	@include ratio(nth($events-size, 2), nth($events-size, 1));

	&::before {
		content: "";
		display: block;
		position: absolute;
		left: 2px;
		top: 2px;
		width: calc(100% - 4px);
		height: calc(100% - 4px);
		background-color: black; }

	img {
		position: absolute;
		left: 0;
		top: 0;
		display: block;
		width: 100%;
		height: 100%;
		max-width: none;
		@include object-fit(cover);
		will-change: opacity;
		transition: {
			property: opacity;
			duration: $animation-speed; } } }

.events-slider__shadow {
	position: absolute;
	left: -$events-inner-space;
	top: -$events-inner-space;
	width: calc(100% + #{$events-inner-space*2});
	height: calc(100% + #{$events-inner-space*2});
	z-index: -1;
	background-color: #fff;
	box-shadow: 0 0 #{$products-shade-size} rgba(0, 0, 0, 0.2);
	pointer-events: none;
	will-change: opacity;
	opacity: 0;
	transition: {
		property: opacity;
		duration: $animation-speed; } }

// hover
.events-slider__inner:hover {
	.events-slider__img {
		img {
			opacity: 0.85; } } }

// active
.swiper-slide-active, .swiper-slide-duplicate-active {
	z-index: 50;

	.events-slider__inner {
		transform: translate(0, -#{nth($events-rang-size, 1)});
		transform: translate3d(0, -#{nth($events-rang-size, 1)}, 0);
		margin-left: -#{nth($events-rang-size, 2)};
		margin-right: -#{nth($events-rang-size, 2)}; }

	.events-slider__shadow {
		opacity: 1; } }

