			visibility: visible; } } }

.loader-cover {
	@include size(100%);
	position: absolute;
	left: 0;
	top: 0;
	z-index: 5;
	background-color: rgba(255, 255, 255, 0.50);
	opacity: 0;
	visibility: hidden;
	transition: {
		property: opacity, visibility;
		duration: $animation-speed; }

	@include media('<tab-lg') {
		position: fixed;
		z-index: 10100; }

	.js-loading & {
		opacity: 1;
		visibility: visible; }

	&::before {
