/* advices, prevs, news, newsr end */

/* advices / prevs */
.prevs {
	position: relative;
	@include clear; }

.prevs__list {
	float: left;
	width: 100%;
	column-count: 3;
	column-gap: 11rem;
	margin-bottom: -5rem;

	@include media('<desk-md') {
		column-gap: 5rem; }

	@include media('<tab-lg') {
		column-count: 2;
		column-gap: 3rem;
		margin-bottom: -3rem; }

	@include media('<mob-lg') {
		column-count: auto; } }

.prevs__item {
	display: inline-block;
	vertical-align: top;
	margin-bottom: 5rem;
	padding-bottom: 3.5rem;
	border-bottom: 2px solid #ccc;

	@include media('<tab-lg') {
		padding-bottom: 2rem;
		margin-bottom: 3rem; } }

/* advices / prevs end */

/* news */
.news {
	position: relative;
	overflow: hidden; }

.news__list {
	position: relative; }

.news__list__holder {
	@include clear-spacing;
	margin: 0 -#{round(nth($grid-space, 2)/2)} -#{nth($grid-space, 1)};

	@include media('<tab-lg') {
		margin: 0 -#{round(nth($grid-space--sm, 2)/2)} -#{nth($grid-space--sm, 1)}; }

	@include media('<mob-lg') {
		margin: 0 -#{round(nth($grid-space--mob, 2)/2)} -#{nth($grid-space--mob, 1)}; } }

.news__item {
	display: inline-block;
	vertical-align: top;
	width: 33.33%;
	letter-spacing: normal;
	padding: 0 #{round(nth($grid-space, 2)/2)};
	margin-bottom: #{nth($grid-space, 1)};

	@include media('<tab-lg') {
		padding: 0 #{round(nth($grid-space--sm, 2)/2)};
		margin-bottom: #{nth($grid-space--sm, 1)}; }

	@include media('<tab-sm') {
		width: 50%; }

	@include media('<mob-lg') {
		padding: 0 #{round(nth($grid-space--mob, 2)/2)};
		margin-bottom: #{nth($grid-space--mob, 1)}; }

	@include media('<mob-sm') {
		width: 100%; }

	@include media('>=tab-sm') {
		&:nth-child(3n-1) {
			text-align: center; }

		&:nth-child(3n) {
			text-align: right; } } }

.news__item__inner {
	display: inline-block;
	vertical-align: top;
	position: relative;
	max-width: 390px;
	height: 100%;
	text-align: left;
	text-decoration: none;
	color: inherit;
	z-index: 1;

	@include media('<tab-sm') {
		max-width: none; }

	&::before {
		content: "";
		display: block;
		position: absolute;
		left: 0;
		top: 0;
		@include size(100%);
		box-shadow: 0 0 18px rgba(0, 0, 0, 0.2);
		background-color: white;
		z-index: -1;
		will-change: opacity;
		opacity: 0;
		transition: {
			property: opacity;
			duration: $animation-speed; } }

	.no-touchevents &:hover {
		&::before {
			opacity: 1; } } }

.news__item__figure {
	margin: 0;
	padding: 0;
	font-size: $font-size;

	img {
		width: 100%; } }

.news__item__body {
	padding: $grid-inside;

	@include media('<tab-lg') {
		padding: $grid-inside--sm; }

	@include media('<mob-lg') {
		padding: $grid-inside--mob; }

	@include media('<mob-sm') {
		padding: 10px 0; } }

/* news */

/* news (by rows) */
.newsr {
	position: relative;
	overflow: hidden; }

.newsr__list {
	position: relative;

	@include media('<mob-lg') {
		@include margin-horizontal(-#{nth($text-layout-space--mob, 2)}); } }

.newsr__item {
	display: block;
	border-top: 2px solid $border-color;

	@include media('<mob-lg') {
		border-width: 1px; }

	&:first-child {
		border-top: none; } }

.newsr__item__inner {
	display: block;
	position: relative;
	text-decoration: none;
	color: inherit;
	padding: 20px 30px;
	z-index: 1;
	@include clear;

	@include media('<tab-lg') {
		padding: 15px 20px; }

	@include media('<mob-lg') {
		padding: 10px nth($text-layout-space--mob, 2); }

	&:hover {
		&::before {
			opacity: 1; } } }

.newsr__item__figure {
	margin: 5px 0 5px 30px;
	padding: 0;
	width: 150px;
	float: right;

	@include media('<tab-lg') {
		margin: 5px 0 5px 20px; }

	@include media('<mob-lg') {
		width: 80px;
		margin: 5px 0 5px nth($text-layout-space--mob, 2); }

	img {
		width: 100%; } }

.newsr__item__body {
	overflow: hidden;
	position: relative;
	padding-left: $newsr-date-width;

	@include media('<mob-lg') {
		//padding-left: $newsr-date-width--sm
		padding-left: 0;
		overflow: visible; } }

.newsr__item__cont {
	@include media('>=mob-md') {
		margin-bottom: 0; } }

.newsr__item__foot {
	position: absolute;
	top: 0;
	left: 0;
	width: $newsr-date-width;
	margin-top: 2px;

	@include media('<mob-lg') {
		position: relative;
		top: auto;
		left: auto;
		margin-top: 0;
		//width: $newsr-date-width--sm
		width: auto; } }

// hover
.no-touchevents .newsr__item__inner:hover {
	.newsr__item__title {
		color: $base-color; } }

/* news (by rows) end */

/* ap */
.ap {
