			font-size: 2.0rem;
			text-align: center; } } }

/* ap end */

/* pseudotabs */
.pseudotabs-nav-wrap {
	margin: 0 0 3rem;

	@include media('<mob-lg') {
		border-bottom: 1px solid $border-color;
		@include margin-horizontal(-#{round(nth($text-layout-space--mob, 1)/2)});
		@include padding-horizontal(#{round(nth($text-layout-space--mob, 1)/2)});
		padding-bottom: 1rem; } }

.pseudotabs-nav {
	margin: 0 -#{round(nth($grid-space, 2)/2)};

	@include media('<tab-lg') {
		margin: 0 -#{round(nth($grid-space--sm, 2)/2)}; }

	@include media('<mob-lg') {
		margin: 0 -#{round(nth($grid-space--mob, 2)/2)}; } }

.pseudotabs-tab {
