// === order result ===
.c-table-result {
	position: relative;
	padding: nth($grid-space, 1) 0;
	line-height: 1;
	text-align: right;

	@include media('<tab-lg') {
		padding: nth($grid-space--sm, 1) 0; }

	@include media('<mob-lg') {
		padding: nth($grid-space--sm, 1) 0; } }

.c-table-result__total {
	display: inline-block;
	vertical-align: middle;
	letter-spacing: normal;
	text-align: right; }

.c-table-result__label {
	display: inline-block;
	vertical-align: middle;
	margin-top: 5px;
	@include font-light;
	@include font(1.4rem, round-float(18/14, 2));
	letter-spacing: 0.05em;
	text-transform: uppercase;
	color: black; }

.c-table-result__price {
	display: inline-block;
	vertical-align: middle;
	margin-left: 10px;
	@include font-alt-bold;
	@include font(3.0rem, round-float(36/30, 2));
	color: black; }

// === for device (after transform) ===
.c-table-resp {
	.c-table, .c-thead, .c-tbody, .c-tfoot, .c-tr, .c-th, .c-td {
		@include media('<tab-lg') {
			display: block; } }

	.c-thead, .c-th {
		@include media('<tab-lg') {
			display: none; } }

	.c-tbody {
		@include media('<tab-lg') {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			align-items: flex-start; } }

	.c-tr {
		@include media('<tab-lg') {
			width: calc(33.3% - 5px);
			margin-bottom: nth($grid-space--sm, 1);
			border: 1px solid $border-color; }

		@include media('<tab-sm') {
			width: calc(50.0% - 5px);
			margin-bottom: nth($grid-space--mob, 1); }

		@include media('<mob-md') {
			width: 100%;
			margin-bottom: round(nth($grid-space--mob, 1)/2); }

		// список заказов в кабинете
		//.orders-history &
		//include media('<tab-lg')
		//width: calc(33.3% - #{round(nth($grid-space, 2)/2)})

		//include media('<tab-sm')
		//width: calc(50.0% - #{round(nth($grid-space, 2)/2)})

		//include media('<mob-lg')
		//width: calc(50.0% - #{round(nth($grid-space--sm, 2)/2)})

		//include media('<mob-md')
 }		//width: 100%

	.c-td {
		@include media('<tab-lg') {
			display: flex;
			align-items: flex-start;
			border: none;
			padding: round(nth($grid-space--mob, 1)/2) nth($grid-space--mob, 2);
			font-size: 1.4rem;
			line-height: 1.8rem; }

		//+media('<mob-md')
		//isplay: block

		&[data-label]::before {
			content: attr(data-label) ':';
			width: 70px;
			min-width: 70px;
			padding-right: 15px;
			@include font-light;
			@include font(1.2rem, 1.8rem);
			text-transform: uppercase;
			display: none;

			@include media('<tab-lg') {
				display: block; }

			//+media('<mob-md')
			//idth: auto
 }			//in-width: 0

		&.c-td--caption {
			@include media('<tab-lg') {
				margin: -1px -1px 1rem;
				font-size: 1.6rem;
				line-height: round-float(20/16, 2);
				border: 1px solid $border-color; }

			&::before {
				@include media('<tab-lg') {
					content: none; } } }

		&.c-td--count {
			@include media('<tab-lg') {
				min-width: 0;
				align-items: center;

				input {
					border: none;
					height: 18px;
					padding: 0;
					width: 60px;
					text-align: left;
					line-height: 18px;
					color: #000 !important; } }

			&::before, .c-td__val {
				//+media('<mob-md')
				//isplay: inline-block
 } }				//ertical-align: top

		&.c-td--sum {
			@include media('<tab-lg') {
				min-width: 0;
				text-align: left;
				font-size: 1.8rem; } } }

	[data-label] .c-td__val {
		@include media('<tab-lg') {
			flex: 1 1; } }

	// hover for device
