	//include media('<tab-lg')
	//.no-touchevents &:hover
	//	.c-td
 }	//		background-color: transparent

/* custom table end */

/* order list */
// === img ===
.img-for-table {
	display: block;
	width: 100px;
	text-decoration: none;
	color: inherit;
	position: relative;
	background-color: #fff;
	margin: 0 auto;

	@include media('<desk-sm') {
		width: 60px; }

	&::after {
		content: "";
		display: block;
		@include ratio(1, 1); }

	img {
		@include cover-abs;
		display: block;
		max-width: none;
		@include object-fit(contain); } }

// === results ===
.c-table-result__total {
	&.hide-before-calc {
		display: none; }

	&.show {
		display: inline-block;

		@include media('<tab-sm') {
			display: block; } } }

// === order empty / no items ===
// собщение, если коризна пуста или нет элементов
.order-calc__empty {
	display: none;
	padding: 8px;
	text-align: center;
	@include font-bold;
	@include font(2.2rem, round-float(26/22, 2));
	color: $base-color; }

// если корзина пуста
.order-calc__hasnt-items {
	.order-calc__empty {
		display: block; }

	.c-table, .c-table-result {
		display: none; } }

/* order list end */
